#header{
    margin-top: 0px;
    height: 80px;
    
    
text-align: center;
align-content: center;
}
#body{
    background-color: white;
     display: flex;
   flex-wrap: wrap;
}
#contact{background-color: white;
   box-shadow: 0 0 16px 2px rgba(0, 0, 0, .4);
width: 350px;
height: fit-content;
margin: 10px;
border-radius: 10px;
text-align: center;
}
#contact:hover{
     border: 2px solid #000;
    
    cursor: pointer;


}
#name{
    background-color: grey;
    color: white;
}
button{
    border: none;
    width: 50%;
    height: 20%;
    cursor: pointer;
}
button:hover{
      border: 2px solid #000;
}
#edit{
    background-color: rgb(233, 241, 127);

}
#delete{
    background-color: red;
}
#header img{
   position: absolute;
   right: 300px;
   top: 20px;
   cursor: pointer;
}

#footer{height: 80px;
    background-color: rgb(235, 124, 161);
    position: absolute;
    bottom: 0px;
    width: 99%;
    text-align: center;
align-content: center;


}